export function isEmpty(value) {
  return value === undefined || value === '' || value === null || JSON.stringify(value) === '{}'
}

export function valiIdCard(id) {
  var format = /^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i
  // 号码规则校验
  if (!format.test(id)) {
    return false
  }
  return true
}

export function valiMobile(number) {
  if (!number) return false
  var format = /^1(3|4|5|7|8)\d{9}$/
  return format.test(number)
}
export function valiPhone(number) {
  if (!number) return false
  var format = /^((\d{3,4}-)?\d{7,8})$|^1(3|4|5|7|8)\d{9}$/
  return format.test(number)
}

// 车牌号验证
export function valiCarNumber(number) {
  var xreg = /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}(([0-9]{5}[DF]$)|([DF][A-HJ-NP-Z0-9][0-9]{4}$))/

  var creg = /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9挂学警港澳]{1}$/

  if (number.length === 7) {
    return creg.test(number)
  } else if (number.length === 8) {
    return xreg.test(number)
  } else {
    return false
  }
}
